    transform: translate(0);
    opacity: 0.7;
  }
  20% {
    transform: translate(-2px, 2px);
    opacity: 0.8;
  }
  40% {
    transform: translate(-2px, -2px);
    opacity: 0.9;
  }
  60% {
    transform: translate(2px, 2px);
    opacity: 0.8;
  }
  80% {
    transform: translate(2px, -2px);
    opacity: 0.7;
  }
  100% {
    transform: translate(0);
    opacity: 0.7;
  }
}
@keyframes glitch-2 {
  0% {
    transform: translate(0);
    opacity: 0.6;
  }
  20% {
    transform: translate(2px, -2px);
    opacity: 0.7;
  }
